.products-page {
    background: linear-gradient(180deg, rgb(255 245 224) 50%, rgb(169 118 118) 100%);
    padding-bottom: 1rem;
    min-height: 85vh;
}

.products-cover-container {
    position: relative;
    background-color: rgb(0, 0, 0);
}

.products-page-cover {
    display: block;
    margin-bottom: 1rem;
    height: 250px;
    width: 100%;
    object-fit: cover;
    opacity: 0.5;
}

.prod-h2 {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 2.1rem;
    font-weight: 500;
    color: rgb(250, 250, 250);
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    padding-right: 0.1rem;
}

.wave {
    position: absolute;
    bottom: 0;
}

.custom-select {
    display: flex;
    position: relative;
    justify-content: flex-end;
}

.select-btn {
    display: flex;
    align-items: center;
    gap: 0.4rem;
    font-size: 0.9rem;
    background-color: white;
    padding: 0.6rem 0.9rem;
    border-radius: 1rem;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
}

.sorting-arrow {
    transition-duration: 0.3s;
}

.turn-arrow {
    transform: rotate(180deg);
    transition-duration: 0.3s;
}

.sortby-text {
    font-weight: 500;
}

.select-btn:hover {
    cursor: pointer;
    background-color: whitesmoke;
    transition-duration: 0.2s;
}

.products-sorting {
    position: absolute;
    top: 38px;
    right: 0;
    z-index: 3;
    font-size: 0.9rem;
    font-weight: 500;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
    background-color: white;
    width: fit-content;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    opacity: 1;
    visibility: visible;
    transition-duration: 0.25s;
}

.sorting-mode {
    padding: 0.3rem 0.5rem;
    white-space: nowrap;
}

.sorting-mode:hover {
    cursor: pointer;
    background-color: rgb(230, 230, 230);
    transition-duration: 0.2s;
}

.products-sorting.hide {
    transition-duration: 0.25s;
    visibility: hidden;
    opacity: 0;
}

.prods-container {
    padding: 1rem;
    display: grid;
    justify-items: center;
    gap: 1rem;
    max-width: 1200px;
    margin: 1rem auto;
}

.arrow-left {
    transform: rotate(90deg);
    padding-top: 0.1rem;
}

.arrow-right {
    transform: rotate(-90deg);
    padding-top: 0.1rem;
}

.page-arrow {
    border-radius: 50%;
    height: 18px;
    width: 18px;
    transition-duration: 0.25s;
    display: flex;
    align-items: center;
    justify-content: center;
}

.page-select {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    padding: 1rem 0 1.5rem;
}

.page-nums {
    display: flex;
    gap: 0.5rem;
}

.page-number {
    background-color: orange;
    border-radius: 50%;
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 0.9rem;
    transition-duration: 0.2s;
}

.page-arrow:hover,
.page-number:hover {
    cursor: pointer;
    background-color: bisque;
    transition-duration: 0.2s;
}

.page-arrow:disabled:hover {
    background-color: rgba(239, 239, 239, 0.3);
    cursor: default;
}

.selected-page {
    background-color: rgb(253, 192, 78);
    border: 1px solid rgba(255, 255, 255, 0.918);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.show-prods-num-ul {
    background-color: rgb(248, 247, 247);
    width: fit-content;
    text-align: center;
    border-radius: 0.5rem;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    position: absolute;
    z-index: 3;
    top: 37px;
    right: 0;
    overflow: hidden;
}

.prods-display-select {
    font-weight: 500;
    width: fit-content;
    position: relative;
}

.show-prods-num {
    padding: 0.3rem 0.5rem;
}

.show-prods-num:hover {
    background-color: rgb(231, 231, 231);
    cursor: pointer;
}

.select-main-container {
    display: flex;
    gap: 1rem;
    justify-content: flex-end;
    flex-wrap: wrap;
    padding: 0.5rem;
}

.rotated {
    transform: rotate(180deg);
}

@media (min-width: 485px) {
    .prods-container {
        grid-template-columns: 46% 46%;
        justify-content: center;
    }
}

@media (min-width: 768px) {
    .prods-container{
        grid-template-columns: repeat(4, minmax(0, 1fr));
    }
}

@media (min-width:992px) {
    .prods-container {
        gap: 1.5rem;
        padding: 0.5rem 0.5rem 1rem;
    }

    .prods-max-width {
        max-width: 1200px;
        margin: 0 auto;
    }
}